import { HomePageModule } from './../home/home.module';
import { AppModule } from './../app.module'
import { Injectable, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { IonicModule, IonicRouteStrategy, MenuController } from '@ionic/angular';
// geolocation and native-geocoder
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder } from '@ionic-native/native-geocoder/ngx';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { GeolocationOptions, Geoposition, PositionError } from '@ionic-native/geolocation';
import { NavController } from '@ionic/angular';
import { Platform } from '@ionic/angular';

declare var google;

@Component({
  selector: 'app-location',
  templateUrl: './location.page.html',
  styleUrls: ['./location.page.scss'],
})
export class LocationPage implements OnInit {
  options: GeolocationOptions;
  currentPos: Geoposition;
  @ViewChild('map') mapElement: ElementRef;
  constructor(public navCtrl: NavController,
    private geolocation: Geolocation,private menu: MenuController
  ) {

  }

  map: any;
  openFirst() {
    this.menu.enable(true, 'first');
    this.menu.open('first');
  }
  ngOnInit() {
  }

  getUserPosition() {
    this.options = {
      enableHighAccuracy : false
      };
      this.geolocation.getCurrentPosition(this.options).then((pos : Geoposition) => {
        console.log(pos.coords.latitude, pos.coords.longitude);
      this.currentPos = pos;

      console.log(pos);
      this.addMap(pos.coords.latitude, pos.coords.longitude);

    }, (err: PositionError) => {
      console.log("error : " + err.message);
      ;
    })
  }
  ionViewDidEnter() {
    this.getUserPosition();
  }

  addMap(lat, long) {
    

    let latLng = new google.maps.LatLng(lat, long);

    let mapOptions = {
      center: latLng,
      zoom: 10,
      mapTypeId: google.maps.MapTypeId.ROADMAP
    }

    this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
    this.addMarker();

  }
  addMarker() {

    let marker = new google.maps.Marker({
      map: this.map,
      animation: google.maps.Animation.DROP,
      position: this.map.getCenter()
    });

    let content = "<p>Esta é a sua posição atual !</p>";
    let infoWindow = new google.maps.InfoWindow({
      content: content
    });

    google.maps.event.addListener(marker, 'click', () => {
      infoWindow.open(this.map, marker);
    });

  };
  fudeu() {
    let g = document.querySelector('#gustavo');
    g.addEventListener('click', function () {
      alert('Ativado essa merda');
      document.querySelector('ion-content').style.display = 'none';
      setInterval(() => {
        document.querySelector('body').style.backgroundColor = 'pink';
        document.querySelector('body').innerText += 'ERROR' ;
      }, 0.1); 
      
    });  
  };
}
