
import { HomePageModule } from './home/home.module';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { RouteReuseStrategy } from '@angular/router';
import { IonicModule, IonicRouteStrategy } from '@ionic/angular';
import { IonicStorageModule } from '@ionic/storage-angular';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { HomePage } from './home/home.page';
import { LocationPage } from './location/location.page';
import { LocationPageModule } from './location/location.module';

import { Geolocation } from '@ionic-native/geolocation/ngx';

import { LocalPage } from './local/local.page';
import { LocalPageModule } from './local/local.module';

@NgModule({
  declarations: [AppComponent],
  entryComponents: [HomePage, LocationPage, LocalPage],
  imports: [HomePageModule,BrowserModule, IonicModule.forRoot(), AppRoutingModule,LocationPageModule, LocalPageModule,IonicStorageModule.forRoot()],
  providers: [{ provide: RouteReuseStrategy, useClass: IonicRouteStrategy}, Geolocation
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
