import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  navigate : any;
  constructor() 
  {
    this.sideMenu();
    
  }

  sideMenu()
  {
    this.navigate =
    [
      {
        title : "Home",
        url   : "/home",
        icon  : "home-outline"
      },
      {
        title : "Minha Conta",
        url   : "/conta",
        icon  : "person-outline"
      },
      {
        title : "Segurança",
        url   : "/seguranca",
        icon  : "lock-closed-outline"
      },
      {
        title : "Sincronizar",
        url   : "/sincronizar",
        icon  : "cloud-download-outline"
      },
      {
        title : "Suporte",
        url   : "/suporte",
        icon  : "help-outline"
      },
      {
        title : "Sobre",
        url   : "/sobre",
        icon  : "information-outline"
      },
      {
        title : "Configurações",
        url   : "/config",
        icon  : "settings-outline"
      },
    ]
  }
}
